import React, { useState, useEffect } from "react";
import "./banner.css";
import axios from "../../../axios";
import { HomeRequests } from "../../../Requests";
import { Link } from "react-router-dom";
import Play from "../../../../icons/play.svg";
import Bookmark from "../../../../icons/bookmark.svg";

function Banner() {
  const [movie, setMovie] = useState([]);
  const [allMovies, setAllMovies] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const req = await axios.get(HomeRequests.fetchNetflixOriginals);
      function setM() {
        setMovie(
          req.data.results[
            Math.floor(Math.random() * req.data.results.length - 1)
          ]
        );
      }
      if (movie.length === 0) {
        setM();
      }
      setAllMovies(req.data.results);
      return req;
    }
    fetchData();
  }, []);

  function truncate(string, n) {
    return string?.length > n ? string.substr(0, n - 1) + "..." : string;
  }

  // console.log(movie);

  // console.log(allMovies);

  return (
    <header
      className="banner"
      style={{
        backgroundSize: "cover",
        backgroundPosition: "center center",
        backgroundImage: `url("https://image.tmdb.org/t/p/original/${movie?.backdrop_path}")`,
        imageRendering: "-webkit-optimize-contrast",
      }}
    >
      <div className="banner-contents">
        <Link
          style={{ width: "fit-content" }}
          to={`/tv/${movie && movie.id}`}
          style={{ textDecoration: "none" }}
          target="_blank"
        >
          <h1 className="banner-title">
            {movie?.name || movie?.title || movie?.original_name}
          </h1>
        </Link>
        <div className="button-cont">
          <div className="buttons">
            <img className="play" src={Play} alt="Play" title="Play"></img>
          </div>
          <div className="buttons">            
            <img
              className="my-list"
              src={Bookmark}
              alt="Bookmark"
              title="Bookmark"
            ></img>
          </div>
        </div>
        <h3 className="banner-description">
          {truncate(`${movie?.overview}`, 950)}
        </h3>
      </div>

      <div className="banner-fade" />
    </header>
  );
}

export default Banner;
